.balloon {
  background-color: $white;
  border-radius: 8px;
  box-shadow: 0px 5px 15px #d4dadd;
  padding: 12px;
  pointer-events: all;
  width: fit-content;

  @include mq('768') {
    min-width: 240px;
  }

  .icon-pointer {
    fill: $white;
    filter: drop-shadow(0px 5px 15px #d4dadd);
    height: 57px;
    left: 50%;
    pointer-events: none;
    position: absolute;
    top: calc(100% - 19px);
    transform: translateX(-50%);
    width: 64px;
  }
}

.balloon-header {
  margin-bottom: 4px;

  .subtitle1 {
    font-weight: bold;
  }
}

.balloon-body {
  padding-bottom: 4px;

  p {
    margin-bottom: 4px;
  }
}

.balloon-footer {
  display: flex;
  position: relative;

  .btn-light {
    margin: 0 auto;
    font-weight: bold;
    font-size: 16px;
  }
}
