.stat {
  @include mq('1440') {
    margin-right: 28px;
  }
}
.stat-wrap {
  padding: 10px 20px 6px;
  display: flex;
  flex-direction: column-reverse;

  @include mq('1440') {
    flex-direction: row;
  }

  .h3 {
    margin-bottom: 4px;
  }

  .body2 {
    color: $grey50;
    margin-bottom: 2px;
  }
}

.stat-filters {
  ul {
    padding-left: 0;
    margin: 8px 0 20px;
    &:last-of-type {
      margin-bottom: 8px;
    }
  }
  .btn-rect {
    background-color: transparent;
    width: 100%;

    &:hover,
    &:focus,
    &.active {
      background-color: $grey10;
      font-weight: 500;
    }
  }
}

.stat-graph-wrap {
  display: flex;
  height: 364px;
  padding-bottom: 8px;
  overflow: auto;
  width: 100%;
  margin-top: 8px;

  @include mq('1440') {
    width: 708px;
    margin-top: 0;
  }
}

.stat-graph-empty {
  align-items: center;
  background-color: $greyLight;
  border-radius: 8px;
  display: flex;
  height: 100%;
  justify-content: center;
  width: 100%;
  p {
    max-width: 350px;
    text-align: center;
  }
}
